<template>
  <div>
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddPage" size="mini" icon="el-icon-plus">添加模板</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region class="region-wrap" @RegionChange="RegionChange" :refresh="false"></region>
          <el-form-item label="创建时间">
            <date-picker-range 
              :defaultRange="searchContent.defaultRange"
              :clearable="false"
              :editable="false"
              @datePickerRangeChange="datePickerRangeChange">
            </date-picker-range>
          </el-form-item>
          <el-form-item label="海报ID">
            <el-input v-model.trim="searchContent.id" size="small" class="search-input" placeholder="请输入海报ID"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="海报标题">
            <el-input v-model.trim="searchContent.posterTitle" size="small" class="search-input" placeholder="请输入海报标题"
              @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="状态">
            <el-select class="search-input" v-model="searchContent.enable" placeholder="请选择订单状态" clearable
              size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in enableOptions">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <div class="table-wraper">
      <el-table
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="70px" prop="id" label="海报ID"></el-table-column>
        <el-table-column min-width="120px" prop="posterTitle" label="海报标题"></el-table-column>
        <el-table-column min-width="90px" prop="regionName" label="城市"></el-table-column>
        <el-table-column min-width="170px" prop="categoryNameList" label="展示商品配置" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column min-width="170px" prop="createTime" label="创建时间"></el-table-column>
        <el-table-column min-width="100px" prop="createSysUserId" label="创建人"></el-table-column>
        <el-table-column min-width="120px" prop="enable" label="状态" :formatter="enableFormatter"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="210">
          <template slot-scope="scope" size="small">
            <div>
              <el-button @click="operateHandel(scope.row)" type="text" size="mini"
                :icon="scope.row.enable === 0 ? 'el-icon-error' : 'el-icon-success'">
                {{scope.row.enable === 0 ? '禁用' : '启用'}}</el-button>
              <el-button type="text" size="mini" icon="el-icon-document-copy" @click="editItem(scope.row)">编辑</el-button>
              <el-button type="text" size="mini" icon="el-icon-document-copy" @click="deleteItem(scope.row)">删除</el-button>
            </div>
            <div>
              <el-button type="text" size="mini" icon="el-icon-document-copy" @click=showPoster(scope.row)>查看海报</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog 
      title="查看海报" 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      :visible.sync="visible" 
      width='400px'>
      <div style="max-height: 400px;overflow-y: auto;">
        <img :src="posterImg" alt="" width="100%">
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visible = false" size="mini">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Region from '@/components/Common/StoreCommon/Region'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import requestUrl from "@/api/StoreMgt.js"
  import * as utils from '@/utils/utils'
  import config from "@/utils/config"
  import Storage from '@/utils/localStorage'
  export default {
    data() {
      return {
        pageSizes: config.pageSizes20,
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          id: '', // 海报活动ID、名称
          posterTitle: '', // 海报标题
          enable: '', // 状态
          defaultRange: [`${utils.timestampToTimeNoHMS(Date.parse(new Date()) - 6 * 86400000)} 00:00:00`, `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`],
          createStartTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()) - 6 * 86400000)} 00:00:00`,
          createEndTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`
        },
        enableOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '启用',
            value: 0
          },
          {
            label: '禁用',
            value: 1
          }
        ],
        tableData: [],
        visible: false,
        posterImg: ''
      }
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region": Region
    },
    created() {
      let searchContentMesg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMesg) {
        this.pageNum = searchContentMesg.pageNum
        this.searchContent = searchContentMesg.searchContent
      }
      Storage.remove('searchContent')
      this.doSearchHandel()
    },
    methods: {
      RegionChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.doSearchHandel(true)
      },
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.createStartTime = result ? result[0] : ''
        this.searchContent.createEndTime = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      doSearchHandel(param) {
        if(param === true) this.pageNum = 1
        this.loading = true
        this.$http.post(requestUrl.getList, {...this.searchContent, pageSize: this.pageSize, pageNum: this.pageNum}).then(res => {
          this.loading = false
          if(res.code === 0) {
            this.tableData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.total = res.data.total
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      goAddPage() {
        this.$router.push("/poster/add")
      },
      editItem(row) {
        this.$router.push(`/poster/edit/${row.id}`)
      },
      deleteItem(row) {
        let params = {
          DIFFCITY: true,
          id: row.id
        }
        this.$confirm('确定要删除吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.delete, {
            ...params
          }).then(res => {
            if (res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {});
      },
      operateHandel(row) {
        let params = {
          DIFFCITY: true,
          id: row.id
        }
        let tipTxt = ''
        if (row.enable === 0) {
          tipTxt = "确定要禁用吗?"
          params.enable = 1
        } else if (row.enable === 1) {
          tipTxt = "确定要启用吗?"
          params.enable = 0
        }
        this.$confirm(tipTxt, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.operate, {
            ...params
          }).then(res => {
            if (res.code === 0) {
              if (row.enable === 0) {
                this.$message.success("禁用成功")
              } else if (row.enable === 1) {
                this.$message.success("启用成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      enableFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return "已启用"
        }else if(cellValue == 1) {
          return "未启用"
        }else {
          return cellValue
        }
      },
      showPoster(row) {
        this.visible = true
        this.posterImg = row.posterImageUrl
      }
    }
  }
</script>


